.main {
	flex-grow: 1;
	position: relative;

	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 100vh;
		z-index: -1;
		background-color: transparent;
		transition: background-color 0.3s;
	}
	&.bg::after {
		background-color: rgba(34, 34, 34, 0.4);
		z-index: 1;
	}
}

// -- title-block
.title-block {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-bottom: 80px;
	@include mw768 {
		margin-bottom: 40px;
	}
	h1 {
		margin-top: 120px;
		margin-bottom: 30px;
		max-width: 825px;
		font-size: 48px;
		font-weight: 700;
		line-height: 130%;
		text-transform: uppercase;
		@include mw768 {
			margin-top: 40px;
			margin-bottom: 15px;
			font-size: 24px;
		}
	}

	.title-badge {
		position: relative;
		display: flex;
		width: 100%;
		align-items: center;
		gap: 10px;
		margin-bottom: 5px;
		flex-wrap: nowrap;
		color: var(--accent);
		font-size: 16px;
		font-weight: 600;
		line-height: 130%;
		white-space: nowrap;
		&::after {
			content: '';
			display: block;
			max-width: 180px;
			width: 100%;
			height: 1px;
			background-color: var(--accent);
		}
	}

	h2 {
		color: var(--dark);
		font-size: 36px;
		font-weight: 600;
		line-height: 130%;
		text-transform: uppercase;
		@include mw768 {font-size: 18px}
	}

	p {
		margin-bottom: 40px;
		max-width: 660px;
		font-size: 18px;
		font-weight: 500;
		line-height: 140%;
		@include mw768 {
			margin-bottom: 30px;
			font-size: 16px;
		}
	}
}
